<template>
	<div>
		<div class="price">
			<el-input
				class="price_input"
				v-model="Price.CurrentPrice"
				placeholder="请输入当前的价格"
			></el-input>
			<el-input
				class="price_input"
				v-model="Price.PreviousPric"
				placeholder="请输入之前的价格"
			></el-input>
		</div>
		<el-input
			class="price_input"
			placeholder="显示涨跌幅"
			v-model="Price.IncreaseAndDecrease"
			:disabled="true"
		>
		</el-input>
		<el-button type="primary" @click="CalculateIncreaseAndDecrease"
			>计算</el-button
		>
	</div>
</template>
<script>
export default {
	name: "variation",
	data() {
		return {
			Price: {
				CurrentPrice: null,
				PreviousPric: null,
				IncreaseAndDecrease: "",
			},
		};
	},
	props: [],
	components: {},
	created() {},
	mounted() {},
	methods: {
		CalculateIncreaseAndDecrease() {
			let Price = this.Price.CurrentPrice / this.Price.PreviousPric;
			if (Price > 1) {
				this.Price.IncreaseAndDecrease = "涨幅" + (Price - 1) * 100 + "%";
			} else {
				this.Price.IncreaseAndDecrease = "跌幅" + -(1 - Price) * 100 + "%";
			}
		},
	},
};
</script>
<style lang="scss" scoped>
	.price {
		display: flex;
	}
	.price_input {
		width: 200px;
	}
</style>